import React, { useState, useEffect } from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

// @ts-ignore
import PublicGoogleSheetsParser from 'public-google-sheets-parser';

import StockList from './components/StockList';

const App = () => {
  const [stockList, setStockList] = useState(null);

  function fetchStockData() {

    const parser = new PublicGoogleSheetsParser();
    parser.parse(atob('MWFYem5zOW1ndWNXZjJodjBuT1JuRnFtTjZLdXhObDlGVEZYaklITlllZzQ'))
      .then((res: any) => {
        const data = res.sort((a: any, b: any) => a.id > b.id ? 1 : -1)
          .slice(0, 20);
        setStockList(data)
    });
  }

  useEffect(() => {
    fetchStockData();
  }, []);

  const useStyles = makeStyles((theme: Theme) =>
    createStyles({
      title: {
        paddingRight: 40,
      },
    }),
  );

  const classes = useStyles();

  return (
    <div>
      <Toolbar>
        <Typography variant="h6" align="left" className={classes.title}>
        전세계 기업 시총 TOP20
        </Typography>
        <Typography variant="caption" align="right">2020-08-24</Typography>
      </Toolbar>
      {stockList != null && <StockList rows={stockList}/>}
    </div>
  )
}

export default App;
